function calculateSince(datetime) {
  const tTime = new Date(datetime);
  const cTime = new Date();
  let sinceMin = Math.round((cTime - tTime) / 60000);
  let since = '';
  if(sinceMin === 0) {
    let sinceSec = Math.round((cTime - tTime) / 1000);
    if(sinceSec<10)
      since = '10 ';
    else if(sinceSec<20)
      since = '20 ';
    else
      since = ' ';
  }
  else if(sinceMin === 1) {
    let sinceSec = Math.round((cTime - tTime) / 1000);
    if(sinceSec === 30)
      since = ' ';
    else if(sinceSec < 60)
      since = '1 ';
    else
      since = ' 1 ';
  }
  else if(sinceMin < 45)
    since = sinceMin + ' ';
  else if(sinceMin > 44 && sinceMin < 60)
    since = ' 1 ';
  else if(sinceMin < 1440){
    let sinceHr = Math.round(sinceMin / 60);
    if(sinceHr === 1)
      since = ' 1 ';
    else
      since = ' ' + sinceHr + ' ';
  }
  else if(1439 < sinceMin && sinceMin < 2880)
    since = '1 ';
  else {
    let sinceDay = Math.round(sinceMin / 1440);
    since = sinceDay + '  ';
  }
  return since;
};


export default calculateSince;



